<template>
  <div class="men-drawer">
    <v-navigation-drawer
      temporary
      v-model="drawer"
      :width="windowsWidth <= 767 ? windowsWidth / 2 : 370"
      class="px-5 pt-0"
    >
      <v-card elevation="0">
        <v-card-title
          class="d-flex justify-space-between align-center w-100"
          style="font-size: 17px; font-weight: 900"
        >
          Shopping Cart <v-icon @click="drawer = false">mdi-close</v-icon>
        </v-card-title>
        <v-list>
          <v-list-item
            class="px-0"
            v-for="cat in categories"
            :key="cat.route"
            @click="
              $router.push({
                name: 'product_category',
                query: { title: cat.title, category: cat.route },
              })
            "
          >
            <v-list-item-title class="nav-link">{{
              cat.title
            }}</v-list-item-title>
          </v-list-item>
          <v-list-group>
            <template v-slot:activator="{ props }">
              <v-list-item class="px-0">
                <v-list-item-title class="nav-link" v-bind="props">
                  Language
                </v-list-item-title>
              </v-list-item>
            </template>
            <v-list-item class="nav-link">
              <v-list-item-title> English </v-list-item-title>
            </v-list-item>
            <v-list-item class="nav-link">
              <v-list-item-title> Dutch </v-list-item-title>
            </v-list-item>
          </v-list-group>
        </v-list>
      </v-card></v-navigation-drawer
    >
  </div>
</template>

<script>
import { productmodule } from "@/stores/product";
import { mapState } from "pinia";
export default {
  computed: {
    ...mapState(productmodule, ["categories"]),
  },
  props: ["windowsWidth"],
  data() {
    return {
      drawer: false,
    };
  },
  inject: ["Emitter"],
  mounted() {
    this.Emitter.on("openMenu", () => {
      this.drawer = true;
    });
  },
};
</script>

<style scoped>
@media (max-width: 580px) {
  .nav-link {
    font-size: 12px;
  }
