<template>
  <div class="layout">
    <v-layout style="position: relative">
      <card-drawer :windowsWidth="windowsWidth"></card-drawer>
      <menu-drawer :windowsWidth="windowsWidth"></menu-drawer>
      <v-main
        :style="`padding-top = ${
          $route.name != 'check_out'
            ? '0px'
            : windowsWidth <= 990
            ? `60px`
            : ' 138px'
        }`"
      >
        <slot></slot>
      </v-main>
      <app-nav v-show="$route.name != 'check_out' && windowsWidth > 990">
      </app-nav>
      <res-bar
        v-show="$route.name != 'check_out' && windowsWidth <= 990"
      ></res-bar>
      <fixed-nav
        v-show="$route.name != 'check_out' && windowsWidth > 990"
      ></fixed-nav>
      <app-footer v-show="$route.name != 'check_out'" />
    </v-layout>
  </div>
</template>

<script>
import AppFooter from "./AppFooter.vue";
import AppNav from "./AppNav.vue";
import CardDrawer from "./CardDrawer.vue";
import ResBar from "./ResBar.vue";
import FixedNav from "./FixedNav.vue";
import MenuDrawer from "./MenuDrawer.vue";
export default {
  data() {
    return {
      drawer: false,
      windowsWidth: 0,
    };
  },
  mounted() {
    this.windowsWidth = window.innerWidth;
    window.onresize = () => {
      this.windowsWidth = window.innerWidth;
    };
  },
  components: {
    AppFooter,
    FixedNav,
    AppNav,
    CardDrawer,
    MenuDrawer,
    ResBar,
  },
};
</script>
