<template>
  <div class="new-products pt-12">
    <div class="title d-flex justify-space-between px-5">
      <h2 style="font-weight: 900; font-size: 30px">New Products</h2>
      <router-link
        style="font-size: 14px"
        class="text-black"
        :to="{
          name: 'product_category',
          query: {
            title: categories[index].title,
            category: categories[index].route,
          },
        }"
        >Shop All</router-link
      >
    </div>
    <v-container fluid>
      <v-row>
        <v-col cols="7 pt-14" v-if="!products.length">
          <v-row>
            <v-col cols="4" v-for="num in 3" :key="num">
              <v-skeleton-loader
                type="image , article , button"
              ></v-skeleton-loader>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="12" md="7" class="pt-14 order-1 order-md-0" v-else>
          <swiper
            :pagination="{ el: '.swipper-pag', clickable: true }"
            :modules="modules"
            :slides-per-view="3"
            :space-between="20"
            :breakpoints="breakpoints"
            class="pb-9 px-5"
          >
            <swiper-slide v-for="item in products" :key="item.id">
              <v-card elevation="0" class="pb-5">
                <v-hover v-slot="{ isHovering, props }">
                  <div
                    class="img-parent position-relative"
                    style="height: 160px; overflow: hidden"
                  >
                    <img
                      :src="
                        shownImg[item.title]
                          ? shownImg[item.title]
                          : item.thumbnail
                      "
                      class="w-100 h-100"
                      :style="`height: 100%; cursor:pointer ; transition : all .5s ease-in-out; scale: ${
                        isHovering ? 1.05 : 1
                      }`"
                      alt=""
                      v-bind="props"
                    />
                    <v-btn
                      class="bg-white btn-change"
                      variant="outlined"
                      denssity="compact"
                      width="60"
                      height="30"
                      style="
                        border-radius: 30px;
                        text-transform: none;
                        position: absolute;
                        opacity: 0;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        font-size: 13px;
                        transition: all ease-in-out 0.2s;
                      "
                      @click="openQuick(item)"
                      >Quick View</v-btn
                    >
                  </div>
                </v-hover>
                <v-card-text class="pl-0 pb-1">
                  ({{ item.title }})
                  {{
                    item.description.length + item.title.length <= 40
                      ? item.description
                      : item.description.slice(0, 40 - item.title.length) +
                        "..."
                  }}
                </v-card-text>
                <v-rating
                  readonly
                  v-model="item.rating"
                  half-increments
                  color="yellow-darken-2"
                  size="x-small"
                ></v-rating>
                <v-card-text class="pl-0 pt-0">
                  <del>${{ item.price }} </del> from
                  <span
                    class="text-red"
                    style="font-weight: 900; font-size: 16px"
                  >
                    ${{
                      Math.ceil(
                        item.price -
                          item.price * (item.discountPercentage / 100)
                      )
                    }}
                  </span>
                </v-card-text>
                <v-btn-toggle v-model="shownImg[item.title]" mandatory>
                  <v-btn
                    v-for="(pic, i) in item.images"
                    :value="pic"
                    size="text-small"
                    :key="i"
                    class="mr-2"
                    rounded
                  >
                    <img
                      :src="pic"
                      width="30"
                      height="30"
                      style="
                        border-radius: 50%;
                        border: 1px solid rgb(135, 135, 135);
                      "
                      alt=""
                    />
                  </v-btn>
                </v-btn-toggle>
                <div class="mt-5">
                  <v-btn
                    class="px-12 py-2"
                    style="
                      border-radius: 30px;
                      width: 30px;
                      text-transform: none;
                    "
                    variant="outlined"
                    @click="
                      $router.push({
                        name: 'product-detail',
                        params: { productId: item.id },
                      })
                    "
                    >Choose Option</v-btn
                  >
                </div>
              </v-card>
            </swiper-slide>
            <div class="swipper-perv"></div>
            <div class="swipper-next"></div>
            <div class="swipper-pag mt-9 d-flex justify-center"></div>
          </swiper>
        </v-col>
        <v-col cols="12" md="5">
          <img src="@/assets/vr-banner.webp" class="w-100" alt="" />
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import { Pagination } from "swiper/modules";
import { VSkeletonLoader } from "vuetify/lib/labs/components.mjs";
import { productmodule } from "@/stores/product";
import { mapState } from "pinia";
export default {
  props: ["products", "index"],
  inject: ["Emitter"],
  computed: {
    ...mapState(productmodule, ["categories"]),
  },
  methods: {
    openQuick(product) {
      this.Emitter.emit("tryOpenquick", product);
    },
  },
  setup() {
    return {
      modules: [Pagination],
    };
  },
  components: {
    Swiper,
    SwiperSlide,
    VSkeletonLoader,
  },
  data() {
    return {
      shownImg: {},
      breakpoints: {
        0: {
          slidesPerView: 1,
        },

        580: {
          slidesPerView: 2,
        },
        1024: {
          slidesPerView: 3,
        },
      },
    };
  },
};
</script>

<style lang="scss">
.new-products {
  .img-parent:hover {
    .btn-change {
      opacity: 1 !important;
    }
  }
}
@media (max-width: 580px) {
  .new-products {
    .img-parent {
      height: 300px !important;
    }
    .swiper-button-next,
    .swiper-button-prev {
      top: 45%;
    }
  }
}
</style>
