<template>
  <div class="product-details mt-16 pt-10">
    <h1 class="mt-16 ml-8">product details</h1>
    <v-container fluid class="mt-6">
      <v-row>
        <v-col cols="12" md="7">
          <img
            :src="tap ? tap : singleproduct.thumbnail"
            class="w-100"
            height="400"
            alt=""
            v-if="!loading"
          />
          <v-skeleton-loader
            v-if="loading"
            type="image,image,image"
          ></v-skeleton-loader>
          <v-tabs class="mt-10" center-active v-model="tap" height="220">
            <v-tab
              v-for="(img, i) in singleproduct.images"
              :key="i"
              v-model="tap"
              class="mx-10"
              :value="img"
            >
              <img :src="img" width="100" height="200" alt=""
            /></v-tab>
          </v-tabs>
        </v-col>
        <v-col cols="12" md="5" class="px-5 pt-0 mt-8 mt-md-0">
          <v-skeleton-loader
            v-if="loading"
            type="article,article,article"
          ></v-skeleton-loader>
          <v-card elevation="0">
            <v-card-title class="px-0" style="font-size: 15px; font-weight: 900"
              >{{ singleproduct.title }} Sample -
              {{ singleproduct.category }} for Sale
            </v-card-title>
            <div class="rating-parent d-flex align-center" style="gap: 10px">
              <v-rating
                readonly
                v-model="singleproduct.rating"
                half-increments
                color="yellow-darken-2"
                size="x-small"
              ></v-rating>
              <span>stock {{ singleproduct.stock }}</span>
            </div>
            <v-card-text
              class="px-0"
              style="font-size: 13px; color: rgb(96, 96, 96)"
            >
              {{ singleproduct.description }}</v-card-text
            >
            <v-card-text
              class="px-0 pt-0"
              style="font-size: 13px; color: rgb(96, 96, 96)"
            >
              Brand : {{ singleproduct.brand }}</v-card-text
            >
            <v-card-text
              class="px-0 pt-0"
              style="font-size: 13px; color: rgb(96, 96, 96)"
            >
              Avability
              {{
                singleproduct.stock > 0 ? "In Stock" : "out of stock"
              }}</v-card-text
            >
            <v-card-text class="pl-0 pt-0">
              <del>${{ singleproduct.price }} </del> from
              <span
                class="text-black"
                style="font-weight: 900; font-size: 16px"
              >
                ${{
                  Math.ceil(
                    singleproduct.price -
                      singleproduct.price *
                        (singleproduct.discountPercentage / 100)
                  )
                }}
              </span>
            </v-card-text>
            <v-card-text class="pl-0 pt-0"> Quantity </v-card-text>
            <div
              class="counter mt-3 px-1"
              style="
                border: 1px solid rgb(201, 201, 201);
                border-radius: 30px;
                width: fit-content;
              "
            >
              <v-icon size="22" @click="quantity > 1 ? quantity-- : 1"
                >mdi-minus</v-icon
              >
              <input
                style="
                  width: 60px;
                  font-size: 14px;
                  border: none;
                  outline: none;
                "
                type="number"
                class="py-3 text-center"
                min="1"
                :value="quantity"
              />
              <v-icon size="22" @click="quantity++">mdi-plus</v-icon>
            </div>
            <v-card-text>
              ${{
                Math.ceil(
                  singleproduct.price -
                    singleproduct.price *
                      (singleproduct.discountPercentage / 100)
                ) * quantity
              }}
            </v-card-text>
            <v-card-actions class="mt-4">
              <v-btn
                variant="outlined"
                @click="addToCart(singleproduct)"
                :loading="btnLoading"
                style="
                  text-transform: none;
                  border-radius: 30px;
                  background-color: black;
                "
                class="w-75 text-white"
                height="50px "
              >
                Add To Cart</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { productmodule } from "@/stores/product";
import { mapActions, mapState } from "pinia";
import { VSkeletonLoader } from "vuetify/lib/labs/components.mjs";
import { cartStore } from "@/stores/cart";
export default {
  components: {
    VSkeletonLoader,
  },
  methods: {
    ...mapActions(productmodule, ["SingleProduct"]),
    ...mapActions(cartStore, ["addItem"]),
    addToCart(item) {
      item.quantity = this.quantity;
      this.btnLoading = true;
      setTimeout(() => {
        this.btnLoading = false;
        this.addItem(item);
        this.Emitter.emit("openCart");
        this.Emitter.emit("showMsg", item.title);
      }, 1000);
    },
  },
  computed: {
    ...mapState(productmodule, ["singleproduct"]),
  },
  async beforeMount() {
    this.loading = true;
    await this.SingleProduct(this.$route.params.productId);
    this.loading = false;
  },
  data() {
    return {
      loading: false,
      btnLoading: false,
      quantity: 1,
      tap: "",
      product: {
        id: 1,
        title: "iPhone 9",
        description: "An apple mobile which is nothing like apple",
        price: 549,
        discountPercentage: 12.96,
        rating: 4.69,
        stock: 94,
        brand: "Apple",
        category: "smartphones",
        thumbnail: "https://i.dummyjson.com/data/products/1/thumbnail.jpg",
        images: [
          "https://i.dummyjson.com/data/products/1/1.jpg",
          "https://i.dummyjson.com/data/products/1/2.jpg",
          "https://i.dummyjson.com/data/products/1/3.jpg",
          "https://i.dummyjson.com/data/products/1/4.jpg",
          "https://i.dummyjson.com/data/products/1/thumbnail.jpg",
        ],
      },
    };
  },
};
</script>

<style scoped>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}
</style>
