<template>
  <div class="flash-deals py-16">
    <div class="title d-flex justify-space-between px-5 mb-10">
      <h2
        style="font-weight: 900; font-size: 30px"
        :class="`text-${titleColor}`"
      >
        {{ title }}
      </h2>
      <router-link
        style="font-size: 14px"
        class="text-black"
        :to="{
          name: 'product_category',
          query: {
            title: categories[index].title,
            category: categories[index].route,
          },
        }"
        >Shop All</router-link
      >
    </div>
    <v-container fluid>
      <v-row v-if="!products.length">
        <v-col cols="12" class="pt-14">
          <v-row>
            <v-col cols="3" v-for="num in 4" :key="num">
              <v-skeleton-loader
                type="image , article , button"
              ></v-skeleton-loader>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
    <swiper
      :pagination="{ el: '.swipper-pag', clickable: true }"
      :modules="modules"
      :slides-per-view="4"
      :space-between="35"
      :breakpoints="breakpoints"
      class="pb-9 px-5"
      :navigation="{ perIcon: 'swipper-perv', nextIcon: 'swipper-next' }"
      :autoplay="{
        delay: 3000,
        pauseOnMouseEnter: true,
        disableOnIneteraction: false,
      }"
      :loop="true"
    >
      <swiper-slide v-for="item in products" :key="item.id">
        <v-card elevation="0" class="pb-5">
          <v-hover v-slot="{ isHovering, props }">
            <div
              class="img-parent position-relative"
              style="height: 200px; overflow: hidden"
            >
              <img
                :src="
                  shownImg[item.title] ? shownImg[item.title] : item.thumbnail
                "
                class="w-100"
                :style="`height: 100%; cursor:pointer ; transition : all .5s ease-in-out; scale: ${
                  isHovering ? 1.05 : 1
                }`"
                alt=""
                v-bind="props"
              />
              <v-btn
                class="bg-white btn-change"
                variant="outlined"
                denssity="compact"
                width="60"
                height="30"
                style="
                  border-radius: 30px;
                  text-transform: none;
                  position: absolute;
                  opacity: 0;
                  top: 50%;
                  left: 50%;
                  transform: translate(-50%, -50%);
                  font-size: 13px;
                  transition: all ease-in-out 0.2s;
                "
                @click="openQuick(item)"
                >Quick View</v-btn
              >
            </div>
          </v-hover>
          <v-card-text class="pl-0 pb-1">
            {{
              `(${item.title}) ${item.description}`.length <= 57
                ? `(${item.title}) ${item.description}`
                : `(${item.title}) ${item.description}`.substring(0, 57)
            }}
          </v-card-text>
          <v-rating
            readonly
            v-model="item.rating"
            half-increments
            color="yellow-darken-2"
            size="x-small"
          ></v-rating>
          <v-card-text class="pl-0 pt-0">
            <del>${{ item.price }} </del> from
            <span class="text-red" style="font-weight: 900; font-size: 16px">
              ${{
                Math.ceil(
                  item.price - item.price * (item.discountPercentage / 100)
                )
              }}
            </span>
          </v-card-text>
          <v-btn-toggle v-model="shownImg[item.title]" mandatory>
            <v-btn
              v-for="(pic, i) in item.images"
              :value="pic"
              size="text-small"
              :key="i"
              class="mr-2"
              rounded
            >
              <img
                :src="pic"
                width="30"
                height="30"
                style="border-radius: 50%; border: 1px solid rgb(135, 135, 135)"
                alt=""
              />
            </v-btn>
          </v-btn-toggle>
          <div class="mt-5">
            <v-btn
              class="px-12 py-2"
              style="border-radius: 30px; text-transform: none"
              variant="outlined"
              @click="
                $router.push({
                  name: 'product-detail',
                  params: { productId: item.id },
                })
              "
              >Choose Option</v-btn
            >
          </div>
        </v-card>
      </swiper-slide>
      <div class="swipper-perv"></div>
      <div class="swipper-next"></div>
      <div class="swipper-pag mt-9 d-flex justify-center"></div>
    </swiper>
  </div>
</template>

<script>
import { Swiper, SwiperSlide } from "swiper/vue";
import { Pagination, Navigation, Autoplay } from "swiper/modules";
import { VSkeletonLoader } from "vuetify/lib/labs/components.mjs";
import { productmodule } from "@/stores/product";
import { mapState } from "pinia";
export default {
  props: ["products", "title", "titleColor", "index"],
  setup() {
    return {
      modules: [Navigation, Pagination, Autoplay],
    };
  },
  computed: {
    ...mapState(productmodule, ["categories"]),
  },
  inject: ["Emitter"],
  methods: {
    openQuick(product) {
      this.Emitter.emit("tryOpenquick", product);
    },
  },
  components: {
    Swiper,
    SwiperSlide,
    VSkeletonLoader,
  },
  data() {
    return {
      shownImg: {},
      breakpoints: {
        0: {
          slidesPerView: 1,
        },
        580: {
          slidesPerView: 2,
        },
        776: {
          slidesPerView: 3,
        },
        990: {
          slidesPerView: 4,
        },
      },
    };
  },
};
</script>

<style lang="scss">
.flash-deals {
  .swiper-button-next,
  .swiper-button-prev {
    widows: 35px;
    height: 35px;
    border: 2px solid rgb(95, 95, 95);
    border-radius: 50%;
    background-color: white;
    top: 41%;
    &::after {
      font-size: 13px;
      font-weight: 900;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      color: rgb(53, 53, 53);
    }
  }
  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
  }

  .img-parent:hover {
    .btn-change {
      opacity: 1 !important;
    }
  }
}
@media (max-width: 580px) {
  .flash-deals {
    .img-parent {
      height: 300px !important;
    }
    .swiper-button-next,
    .swiper-button-prev {
      top: 45%;
