<template>
  <div class="product-category pt-15 mt-15">
    <v-container>
      <h1 class="text-center mt-15">{{ $route.query.title }}</h1>
      <v-card :loading="loading" min-height="700px" class="pt-5" elevation="0">
        <v-row>
          <v-col
            class="px-10 px-sm-5 mt-5 mt-lg-0"
            cols="12"
            sm="6"
            md="4"
            lg="3"
            v-for="item in ProductCategoryarray.products"
            :key="item.id"
          >
            <v-card elevation="0">
              <v-hover v-slot="{ isHovering, props }">
                <div
                  class="img-parent position-relative"
                  style="height: 200px; overflow: hidden"
                >
                  <img
                    :src="
                      shownImg[item.title]
                        ? shownImg[item.title]
                        : item.thumbnail
                    "
                    class="w-100"
                    :style="`height: 100%; cursor:pointer ; transition : all .5s ease-in-out; scale: ${
                      isHovering ? 1.05 : 1
                    }`"
                    alt=""
                    v-bind="props"
                  />
                  <v-btn
                    class="bg-white btn-change"
                    variant="outlined"
                    denssity="compact"
                    width="90"
                    height="30"
                    style="
                      border-radius: 30px;
                      text-transform: none;
                      position: absolute;
                      opacity: 0;
                      top: 50%;
                      left: 50%;
                      transform: translate(-50%, -50%);
                      font-size: 13px;
                      transition: all ease-in-out 0.2s;
                    "
                    @click="openQuick(item)"
                    >Quick View</v-btn
                  >
                </div>
              </v-hover>
              <v-card-text class="pl-0 pb-1">
                {{
                  `(${item.title}) ${item.description}`.length <= 57
                    ? `(${item.title}) ${item.description}`
                    : `(${item.title}) ${item.description}`.substring(0, 57)
                }}
              </v-card-text>
              <v-rating
                readonly
                v-model="item.rating"
                half-increments
                color="yellow-darken-2"
                size="x-small"
              ></v-rating>
              <v-card-text class="pl-0 pt-0">
                <del>${{ item.price }} </del> from
                <span
                  class="text-red"
                  style="font-weight: 900; font-size: 16px"
                >
                  ${{
                    Math.ceil(
                      item.price - item.price * (item.discountPercentage / 100)
                    )
                  }}
                </span>
              </v-card-text>
              <v-btn-toggle v-model="shownImg[item.title]">
                <v-btn
                  v-for="(pic, i) in item.images"
                  :value="pic"
                  size="text-small"
                  :key="i"
                  class="mr-2"
                  rounded
                >
                  <img
                    :src="pic"
                    width="30"
                    height="30"
                    style="
                      border-radius: 50%;
                      border: 1px solid rgb(135, 135, 135);
                    "
                    alt=""
                  />
                </v-btn>
              </v-btn-toggle>
              <div class="mt-5">
                <v-btn
                  class="px-12 py-2"
                  style="border-radius: 30px; text-transform: none"
                  variant="outlined"
                  @click="
                    $router.push({
                      name: 'product-detail',
                      params: { productId: item.id },
                    })
                  "
                  >Choose Option</v-btn
                >
              </div>
            </v-card>
          </v-col>
        </v-row>
      </v-card>
    </v-container>
  </div>
</template>

<script>
import { productmodule } from "@/stores/product";
import { mapActions, mapState } from "pinia";
export default {
  inject: ["Emitter"],
  methods: {
    ...mapActions(productmodule, ["ProductCategory"]),
    openQuick(product) {
      this.Emitter.emit("tryOpenquick", product);
    },
  },
  computed: {
    ...mapState(productmodule, ["ProductCategoryarray"]),
  },
  async mounted() {
    if (!this.$route.query.category) {
      this.$router.go(-1);
    }
    this.loading = true;
    await this.ProductCategory(this.$route.query.category);
    this.loading = false;
  },
  data() {
    return {
      shownImg: {},
      loading: false,
    };
  },
  watch: {
    async $route() {
      if (this.$route.name == "product_category") {
        document.documentElement.scrollTo(0, 0);
        this.loading = true;
        await this.ProductCategory(this.$route.query.category);
        this.loading = false;
      }
    },
  },
};
</script>

<style lang="scss">
.product-category {
  .img-parent:hover {
    .btn-change {
      opacity: 1 !important;
    }
  }
}
</style>
