import { defineStore } from "pinia";
import axios from "axios";

export const productmodule = defineStore("productmodule", {
  state() {
    return {
      flashDeals: [],
      NewProduct: [],
      SmartPhone: [],
      Fragrances: [],
      Groceries: [],
      ProductCategoryarray: [],
      singleproduct: "",
      categories: [
        {
          title: "smart phones",
          route: "smartphones",
        },
        {
          title: "laptops",
          route: "laptops",
        },
        {
          title: "Fragrances ",
          route: "fragrances",
        },
        {
          title: "skincare ",
          route: "skincare",
        },
        {
          title: "Groceries ",
          route: "groceries",
        },
        {
          title: "home-decoration",
          route: "home-decoration",
        },
        {
          title: "furniture ",
          route: "furniture",
        },
      ],
    };
  },
  actions: {
    async getProduct() {
      try {
        const response = await axios.get("https://dummyjson.com/products");
        const products = response.data.products;
        this.NewProduct = products.filter(
          (product) => product.category === "laptops"
        );
        this.SmartPhone = products.filter(
          (product) => product.category === "smartphones"
        );
        this.Fragrances = products.filter(
          (product) => product.category === "fragrances"
        );
        this.Groceries = products.filter(
          (product) => product.category === "groceries"
        );
        this.flashDeals = products.slice(0, 8);
      } catch (err) {
        console.error(err);
      }
    },
    async ProductCategory(category) {
      await axios
        .get(`https://dummyjson.com/products/category/${category}`)
        .then((res) => (this.ProductCategoryarray = res.data));
    },
    async SingleProduct(productId) {
      await axios
        .get(`https://dummyjson.com/products/${productId}`)
        .then((res) => (this.singleproduct = res.data));
    },
  },
});
