<template>
  <div class="quick-v">
    <v-dialog v-model="dialog" max-width="900" max-height="500">
      <v-icon
        @click="dialog = false"
        style="
          position: absolute;
          top: -14px;
          right: -14px;
          color: rgb(187, 172, 172);
          background-color: black;
          font-size: 18px;
          padding: 13px;
          z-index: 10;
        "
        >mdi-close</v-icon
      >
      <v-card elevation="0" class="content-card">
        <v-container fluid class="bg-white pt-10 px-10">
          <v-row>
            <v-col cols="12" sm="7">
              <img
                :src="tap ? tap : products.thumbnail"
                class="w-100"
                height="400"
                alt=""
                v-if="!loading"
              />
              <v-skeleton-loader
                v-if="loading"
                type="image,image,image"
              ></v-skeleton-loader>
              <v-tabs class="mt-10" center-active v-model="tap" height="130">
                <v-tab
                  v-for="(img, i) in products.images"
                  :key="i"
                  v-model="tap"
                  class="mx-10"
                  :value="img"
                >
                  <img :src="img" width="70" height="100" alt=""
                /></v-tab>
              </v-tabs>
            </v-col>
            <v-col cols="12" md="5" class="px-5 pt-0">
              <v-skeleton-loader
                v-if="loading"
                type="article,article,article"
              ></v-skeleton-loader>
              <v-card elevation="0">
                <v-card-title
                  class="px-0"
                  style="font-size: 15px; white-space: nowrap; font-weight: 900"
                  >{{ products.title }} Sample - {{ products.category }} for
                  Sale
                </v-card-title>
                <div
                  class="rating-parent d-flex align-center"
                  style="gap: 10px"
                >
                  <v-rating
                    readonly
                    v-model="products.rating"
                    half-increments
                    color="yellow-darken-2"
                    size="x-small"
                  ></v-rating>
                  <span>stock {{ products.stock }}</span>
                </div>
                <v-card-text
                  class="px-0"
                  style="font-size: 13px; color: rgb(96, 96, 96)"
                >
                  {{ products.description }}</v-card-text
                >
                <v-card-text
                  class="px-0 pt-0"
                  style="font-size: 13px; color: rgb(96, 96, 96)"
                >
                  Brand : {{ products.brand }}</v-card-text
                >
                <v-card-text
                  class="px-0 pt-0"
                  style="font-size: 13px; color: rgb(96, 96, 96)"
                >
                  Avability
                  {{
                    products.stock > 0 ? "In Stock" : "out of stock"
                  }}</v-card-text
                >
                <v-card-text class="pl-0 pt-0">
                  <del>${{ products.price }} </del> from
                  <span
                    class="text-black"
                    style="font-weight: 900; font-size: 16px"
                  >
                    ${{
                      Math.ceil(
                        products.price -
                          products.price * (products.discountPercentage / 100)
                      )
                    }}
                  </span>
                </v-card-text>
                <v-card-text class="pl-0 pt-0"> Quantity </v-card-text>
                <div
                  class="counter mt-3 px-1"
                  style="
                    border: 1px solid rgb(201, 201, 201);
                    border-radius: 30px;
                    width: fit-content;
                  "
                >
                  <v-icon size="22" @click="quantity > 1 ? quantity-- : 1"
                    >mdi-minus</v-icon
                  >
                  <input
                    style="
                      width: 60px;
                      font-size: 14px;
                      border: none;
                      outline: none;
                    "
                    type="number"
                    class="py-3 text-center"
                    min="1"
                    :value="quantity"
                  />
                  <v-icon size="22" @click="quantity++">mdi-plus</v-icon>
                </div>
                <v-card-text>
                  ${{
                    Math.ceil(
                      products.price -
                        products.price * (products.discountPercentage / 100)
                    ) * quantity
                  }}
                </v-card-text>
                <v-card-actions class="mt-4">
                  <v-btn
                    @click="addToCart(products)"
                    variant="outlined"
                    :loading="btnLoading"
                    style="
                      text-transform: none;
                      border-radius: 30px;
                      background-color: black;
                    "
                    class="w-75 text-white"
                    height="50px "
                  >
                    Add To Cart</v-btn
                  >
                </v-card-actions>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { VSkeletonLoader } from "vuetify/lib/labs/components.mjs";
import { cartStore } from "@/stores/cart";
import { mapActions } from "pinia";
export default {
  components: {
    VSkeletonLoader,
  },
  methods: {
    ...mapActions(cartStore, ["addItem"]),
    addToCart(item) {
      item.quantity = this.quantity;
      this.btnLoading = true;
      setTimeout(() => {
        this.btnLoading = false;
        this.addItem(item);
        this.Emitter.emit("openCart");
        this.Emitter.emit("showMsg", item.title);
        this.dialog = false;
      }, 1000);
    },
  },
  inject: ["Emitter"],

  mounted() {
    this.Emitter.on("tryOpenquick", (data) => {
      this.products = data;
      this.dialog = true;
    });
  },
  data() {
    return {
      loading: false,
      btnLoading: false,
      products: "",
      quantity: 1,
      dialog: false,
      tap: "",
    };
  },
};
</script>

<style scoped>
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
}
</style>
