<template>
  <div class="order-success">
    <v-dialog v-model="dialog" width="50%" persistent>
      <v-card class="text-center py-7">
        <div class="text-center">
          <v-icon
            size="75"
            color="green"
            style="
              color: green;
              background: white;
              width: 100px;
              height: 100px;
              border-radius: 50%;
              font-size: 76px;
              border: 1px solid green;
            "
            >mdi-check</v-icon
          >
        </div>
        <v-card-title
          style="font-size: 30px; font-weight: bold; color: rgb(96, 96, 96)"
        >
          Order Placed Succfuly
        </v-card-title>
        <v-card-text style="font-size: 17px; color: rgb(96, 96, 96)"
          >Lorem ipsum, dolor sit amet consectetur adipisicing elit. Nihil
          libero nisi ducimus vitae obcaecati beatae quam corporis, quibusdam a
          nobis? Iure, voluptas illum. Cupiditate iusto, amet rem voluptatem eum
          delectus?</v-card-text
        >
        <v-card-actions class="mt-5 justify-center"
          ><v-btn variant="elevated" color="blue" @click="resetData"
            >Got it</v-btn
          ></v-card-actions
        >
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { cartStore } from "@/stores/cart";
import { mapActions } from "pinia";
export default {
  data() {
    return {
      dialog: false,
    };
  },
  methods: {
    ...mapActions(cartStore, ["resetItems"]),
    resetData() {
      this.resetItems();
      this.dialog = false;
      this.$router.push({ name: "home" });
    },
  },
  watch: {
    dialog(newVal) {
      if (!newVal) {
        setTimeout(() => {
          this.$emit("new-popup");
        }, 200);
      }
    },
  },
  props: ["popup"],
  mounted() {
    this.dialog = this.popup;
  },
};
</script>

<style></style>
